      hospitalItems: [
        { distance: "1.0", name: "无锡市第一人民医院", isactive: 0 },
        { distance: "1.0", name: "无锡市第一人民医院", isactive: 1 },
        { distance: "1.0", name: "无锡市第一人民医院", isactive: 1 },
      ],

      otherItems: [
        { text: "换胎", value: 1, isactive: 0 },
        { text: "搭电", value: 2, isactive: 1 },
        { text: "加油", value: 3, isactive: 1 },
        { text: "洗车", value: 4, isactive: 1 },
      ],
      modelArray: [
        { name: "型号1" },
        { name: "型号2" },
        { name: "型号3" },
        { name: "型号4" },
      ],
      companyArray: [
        { name: "公司1" },
        { name: "公司2" },
        { name: "公司3" },
        { name: "公司4" },
      ],
      params: {
        status: 1,
        name: null,
        phone: null,
        startAddress: null,
        endAddress: null,
        appointDate: null,
        description: null,
        fileId: [],

        accidentCarNum: 1,
        injuredNum: 0,
        isDrive: 0,
        ambulanceNum: 0,
        trailerNum: 0,
        isTrafficPolice: 0,
        isFirePerson: 0,
        isEscort: 0,
        injuredType: null,
        hospitalId: null,
      },
    };
  },
  onLoad() {
    uni.setStorageSync("address", null);
    uni.setStorageSync("endAddress", null);
    uni.getLocation({
      type: "wgs84",
      geocode: true,
      altitude: true,
      isHighAccuracy: true,
      success: function (res) {
        console.log(res);
        var that = this;
        var map = new qqmapsdk({
          key: "PLUBZ-U3CRX-25I4F-ZW5YC-HSCWO-KKBQ2",
          // key: 'TXZBZ-HPMWU-4QIVM-2ISK7-2L7GK-7XBE4'
          // key:'CNGBZ-3IWKP-QVIDZ-LOA2N-FJKTO-YCBR6'
        });
        //通过这个方法来实现经纬度反推省市区
        map.reverseGeocoder({
          location: {
            latitude: res.latitude,
            longitude: res.longitude,
          },
          success: function (res) {
            uni.setStorageSync(
              "startAddress",
              res.result.address + res.result.formatted_addresses.recommend
            );
            uni.setStorageSync("latitude", res.result.location.lat);
            uni.setStorageSync("longitude", res.result.location.lng);
            console.log("res =>123", uni.getStorageSync("startAddress"));
          },
        });
      },
    });
    setTimeout(() => {
      this.getHospitalList();
    }, 1500);
  },
  // onLoad() {
  // 	const QQMapWX = new qqmapsdk({
  //   	key: 'CNGBZ-3IWKP-QVIDZ-LOA2N-FJKTO-YCBR5',
  //   });
  //   uni.getLocation({
  //     type: 'wgs84',
  //     geocode: true,
