  //     complete: () => {
  //       // 解析地址
  //       QQMapWX.reverseGeocoder({
  //         locationaaa: {
  //           latitude: this.latitude,
  //           longitude: this.longitude
  //         },
  //         success: function(res) {
  //           console.log("解析地址成功");
  //           console.log(res);
  //           // 省
  //           let province = res.result.ad_info.province;
  //           // 市
  //           let city = res.result.ad_info.city;
  //           console.log(province);
  //           console.log(city);
  //         },
  //         fail: function(res) {
  //           uni.showToast({
  //             title: '定位失败',
  //             duration: 2000,
  //             icon: "none"
  //           })
  //           console.log(res);
  //         },
  //         complete: function(res) {
  //           console.log(res);
  //         }
  //       })
  //     }
  //   })
  // },
  mounted() {
    if (uni.getStorageSync("address") == null) {
      this.params.startAddress = uni.getStorageSync("startAddress");
    } else {
      this.params.startAddress = uni.getStorageSync("address");
    }
    this.params.endAddress = uni.getStorageSync("endAddress");
  },
  methods: {
    chooseLocation: function (e) {
      if (e == 0) {
        uni.chooseLocation({
          success: (res) => {
            console.log("res =>", res);
            this.hasLocation = true;
            this.location = formatLocation(res.longitude, res.latitude);
            this.params.startAddress = res.address + res.name;
          },
        });
      } else if (e == 1) {
        uni.chooseLocation({
          success: (res) => {
            (this.hasLocation = true),
              (this.location = formatLocation(res.longitude, res.latitude)),
              (this.params.endAddress = uni.setStorageSync(
                "endAddress",
                res.address + res.name
              ));
          },
        });
      }
    },
    successImage(e) {
      let imgArray = e && JSON.parse(e.data);
      this.params.fileId.push(imgArray.data && imgArray.data.id);
      console.log(this.params.fileId);
    },
    goToSafetyWarning: function (e) {
      uni.navigateTo({
        url: "/pages/safetyWarning/index",
      });
    },
    handleSelectCarNo(item, index) {
      this.params.accidentCarNum = item.type;
      if (index === 0) {
        this.carNoItems[index].isactive = 0;
        this.carNoItems[index + 1].isactive = 1;
        this.carNoItems[index + 2].isactive = 1;
      } else if (index === 1) {
        this.carNoItems[index].isactive = 0;
        this.carNoItems[index - 1].isactive = 1;
        this.carNoItems[index + 1].isactive = 1;
      } else if (index === 2) {
        this.carNoItems[index].isactive = 0;
        this.carNoItems[index - 1].isactive = 1;
        this.carNoItems[index - 2].isactive = 1;
      }
    },
    handleSelectHospitalInfo(item, index) {
      this.params.hospitalId = item.id;
      let hospitalArray = this.hospitalItems;
      hospitalArray.forEach((_item, _index) => {
        _item.isactive = false;
        if (index == _index) this.hospitalItems[index].isactive = true;
      });
    },
    handleSelectOtherInfo(item, index) {
      this.otherItems[index].isactive = 0;
    },
    bindPickerChange: function (e) {
      this.index = e.detail.value;
      this.params.injuredType = this.array[e.detail.value].type;
    },

    handleInjuredNumReduction() {
      if (this.params.injuredNum >= 1) {
        this.params.injuredNum--;
      } else {
        this.params.injuredNum = 0;
      }
      console.log(this.params.injuredNum);
      if (this.params.injuredNum == 0) {
        this.isNumber = true;
        return;
      } else {
        this.isNumber = false;
      }
    },
    handleInjuredNumAdd() {
      this.params.injuredNum++;
      this.isNumber = false;
      console.log(this.params.injuredNum);
      // if (this.params.injuredNum !== 0) {
      //   this.isNumber = false;
      // }
    },
    handleAmbulanceNumReduction() {
      if (this.params.ambulanceNum >= 1) {
        this.params.ambulanceNum--;
      } else {
        this.params.ambulanceNum = 0;
      }
      console.log(this.params.ambulanceNum);
      if (this.params.ambulanceNum == 0) {
        this.isAmbulanceNum = true;
        return;
      } else {
        this.isAmbulanceNum = false;
      }
    },
    handleAmbulanceNumAdd() {
      this.params.ambulanceNum++;
      this.isAmbulanceNum = false;
    },
    handleTrailerNumReduction() {
      if (this.params.trailerNum >= 1) {
        this.params.trailerNum--;
      } else {
        this.params.trailerNum = 0;
      }
      console.log(this.params.trailerNum);
      if (this.params.trailerNum == 0) {
        this.isTrailerNum = true;
        return;
      } else {
        this.isTrailerNum = false;
      }
    },
    handleTrailerNumAdd() {
      this.params.trailerNum++;
      this.isTrailerNum = false;
    },
    getHospitalList() {
      console.log(uni.getStorageSync("longitude"));
      console.log(uni.getStorageSync("latitude"));
      let longitude = uni.getStorageSync("longitude") || 0;
      let latitude = uni.getStorageSync("latitude") || 0;

      this.$http
        .hospitalList({ longitude: longitude, latitude: latitude })
        .then((res) => {
          const { status, data } = res;
          if (status == 500) {
            uni.showToast({
              title: res.message,
              icon: "none",
              duration: 2000,
            });
          } else {
            console.log(data);
            data.forEach((_item, _index) => {
              _item.distance = _item.distance.toFixed(2);
              this.hospitalItems[_index].isactive = false;
            });
            this.hospitalItems = data;
            console.log(this.hospitalItems);
            // uni.navigateTo({
            //   url: "/pages/emergencyCourse/emergencyCourseDetail?id=" + res.data.id,
            // });
          }
        });
    },

    saveInfo() {
      let img = this.params.fileId;
      this.params.file = img.toString();
      this.params.otherNeed = this.checkbox6.toString();
      this.params.address = this.params.startAddress;
      console.log(this.params);
      this.$http.planRoute(this.params).then((res) => {
        console.log(res);
        const { status } = res;
        if (status == 200) {
          uni.showToast({
            title: "添加成功",
            icon: "success",
            duration: 2000,
          });
          uni.navigateTo({
            url: "/pages/history/index",
          });
        } else {
          uni.showToast({
            title: res.message,
            icon: "none",
            duration: 2000,
          });
        }
      });
    },
    handleHistoryList() {
      uni.navigateTo({
        url: "/pages/history/index",
      });
    },
  },
};
</script>

